::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(9, 69, 9);
}

a {
  font-family: 'MuseoModerno', cursive;
  text-decoration: double;
  color: inherit;
  background-color: transparent;
}

ul,
ol {
  list-style: none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

h5 {
  font-family: 'Tourney', cursive;
}

h1 {
  font-family: 'MuseoModerno', cursive;
}

h2,
h3,
h4,
h6 {
  font-family: 'Text Me One', sans-serif;
  font-size: 100%;
  font-weight: normal;
  font-weight: normal;
}

img,
video {
  display: block;
  max-width: 100%;
}

.btn {
  border: 4px double rgb(37, 176, 44);
  padding: 10px 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  font-weight: 400;
  outline: none;
}

.PrimaryBtn {
  background: gray(247, 248, 247, 0.67);
  color: #0a0a0a;
}

.PrimaryBtn:hover {
  background: gray(30, 195, 200);
  color: #010606;
}

.SecondarBtn:hover {
  background: gray(30, 195, 200);
  color: #fff;
}

.btn2 {
  margin-right: 1rem;
}

.Container {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

.BigCard {
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-right: 2rem;
  padding-left: 2rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  /* background: url("https://i.imgur.com/gb3zbIS.png"); */
  box-shadow: green(0, 0, 0, 0.05) 5px 5px 10px;
  box-shadow: 0 10px 15px -3px green(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.AboutBio,
.tagline2 {
  font-family: 'Text Me One', sans-serif;

  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  color: #151418;

  padding-top: 10px;
}

.tagline2 {
  font-family: 'MuseoModerno', cursive;
  text-decoration: underline green;
  font-size: 44px;
  margin-top: 10rem;
  margin-bottom: 1.5rem;
  color: green;
  text-align: center;
}

.SectionTitle {
  font-family: 'MuseoModerno', cursive;
  color: green;
  font-size: 44px;
  margin-bottom: 1.5rem;
  text-align: center;
  text-decoration: underline green;
}

.ProjectWrapper {
  margin-top: -1rem;
}

.highlight0,
.highlight1,
.highlight2, .highlight3 {
  font-family: 'MuseoModerno', cursive;
  color: green;
}

/* ------------------------------------ Media for Container */
@media (min-width: 576px) {
  .Container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .Container {
    max-width: 720px;
  }
  .ProjectWrapper {
    margin-top: -5rem;
  }
}
@media (min-width: 992px) {
  .Container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .Container {
    max-width: 1000px;
  }
}
